import Nav from "../components/Nav";
import IDCard from "../components/IDCard";
import Banner from "../components/Banner";
import SettingsBtn from "../components/SettingsBtn";

function Jumin() {
  return (
    <>
      <Nav parameter={0}/>
      <div className="main">
        <IDCard />
        <Banner />
        <SettingsBtn />
      </div>
    </>
  );
}

export default Jumin;
