import React, { useState, useEffect } from "react";

export default function Progressbar() {
  const [filled, setFilled] = useState(100);
  const [remainTime, setRemainTime] = useState(30);

  useEffect(() => {
    if (remainTime >= 0) {
      setTimeout(() => setRemainTime((prev) => (prev += -1)), 1000);
    }else{
        setRemainTime(30)
        setFilled(100)
    }
    }
  , [remainTime]);

  useEffect(() => {
    if (filled > 0) {
      setTimeout(() => setFilled((prev) => (prev += -0.36)), 100);
    }
  }, [filled]);
  return (
    <div>
      <div className="progressbar">
        <div
          style={{
            height: "100%",
            width: `${filled}%`,
            background: "linear-gradient(90deg, rgba(235,199,199,1) 0%, #FF3A4A 100%)"
            
          }}
        ></div>
        <span className="progressPercent">  {remainTime}</span>
      </div>
    </div>
  );
}
