const SettingsBtn = ({ color }) => {
  return (
    <div
      style={
        color === "red"
          ? { backgroundColor: "#FF3A4A" }
          : color === "purple"
          ? { backgroundColor: "#6250FE" }
          : { backgroundColor: "#466fff" }
      }
      className="st-button"
    >
      <span></span>
    </div>
  );
};

export default SettingsBtn;
